@font-face {
    font-family: "Sansation";
    src: url("./assets/fonts/sansation/Sansation_Light.ttf") format("truetype");
}

@font-face {
    font-family: "Rubik Glitch";
    src: url("./assets/fonts/Rubik_Glitch/RubikGlitch-Regular.ttf") format("truetype");
}

/* Hide the default scrollbar */
::-webkit-scrollbar {
    width: 0.5rem;
    background-color: transparent;
}

/* Styling the scrollbar handle */
::-webkit-scrollbar-thumb {
    background: linear-gradient(135deg, rgb(20, 245, 174), rgb(165, 140, 254)); /* Dark-grey color */
    border-radius: 10px; /* Rounded handle */
}

/* Add a little padding to avoid overlap with content */
body {
    font-family: "Sansation", sans-serif;
    background-color: rgb(31, 31, 38);
    font-size: xx-large;
    color: rgb(235, 233, 242);
    padding: 2em;
    margin: auto;
}

.hardstuff {
    font-weight: lighter;
    font-family: "Rubik Glitch", sans-serif;
    background: linear-gradient(135deg, rgb(20, 245, 174), rgb(165, 140, 254));
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
}

a {
    background: linear-gradient(135deg, rgb(20, 245, 174), rgb(165, 140, 254));
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    font-weight: lighter;
    font-family: "Rubik Glitch", sans-serif;
}

.hardstuffnocolor {
    font-weight: lighter;
    font-family: "Rubik Glitch", sans-serif;
}

.section {
    margin: 10em 0 10em 0;
    opacity: 0;
    animation-fill-mode: both; /* Make sure the element retains the state after the animation */
}

.section .content {
    max-width: 1000px;
    margin: 0 auto;
    margin-top: 3em;
    margin-bottom: 3em;
}

h1 {
    font-size: 10rem;
    margin-bottom: 1rem;
    color: rgb(255, 255, 255);
}

h2 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
    color: rgb(255, 255, 255);
}

h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    color: rgb(255, 255, 255);
}

.lock-header {
    font-size: 1em;
    padding: 20px;
    position: fixed;
    text-align: left;
    left: 0px;
    top: 0px;
    width: 100%;
    background: linear-gradient(180deg, rgb(20, 20, 20), rgba(165, 140, 254, 0));
    z-index: 99;
}

.header {
    align-items: center;
    padding: 0px;
    margin: 15% 0 25% 0;
}

.lock-footer {
    font-size: 1em;
    padding: 20px;
    position: fixed;
    text-align: left;
    left: 0px;
    bottom: 0px;
    width: 100%;
    height: 1em;
    background: linear-gradient(0, rgb(20, 20, 20), rgba(165, 140, 254, 0));
}

.footer {
    font-size: small;
    margin: 20px;
}

.footer a {
    color: rgb(224, 242, 133);
    text-decoration: none;
    margin-right: 1rem;
}

button {
    border: none;
    padding: 1rem 2rem;
    font-size: 0.8em;
    font-weight: bold;
    cursor: pointer;
    background-color: rgb(235, 233, 242);
    transition: background-color 0.3s ease;
    border-radius: 1em;
    color: rgb(39, 48, 41);
    margin: 0.5em;
    font-family: inherit;
}

.hollowed {
    background-color: initial;
    border: 2px solid rgb(235, 233, 242);
    color: rgb(235, 233, 242);
}

button:hover {
    background-color: rgb(224, 242, 133);
}

.hollowed:hover {
    color: rgb(39, 48, 41);
}

.iconned_button {
    border: 4px solid rgb(235, 233, 242);
    background: rgb(235, 233, 242);
    cursor: pointer;
    margin: 1em;
    width: 70%;
    margin-left: 15%;
}

.iconned_button_img {
    height: 32px;
    flex-shrink: 0; /* prevents the image from shrinking*/
    padding: 10px;
}

.iconned_button:hover {
    background-color: rgb(224, 242, 133);
    border-color: rgb(224, 242, 133);
}

.flex_box {
    display: flex;
    align-items: flex-start;
    max-height: 500px;
    height: 500px;
    margin-bottom: 0px;
}

.flex_box_content {
    margin: 5px;
    padding: 5px;
    transition: flex 0.5s;
    overflow: hidden;
    height: 100%;
}

.footer p {
    margin: 0;
}

/* Intersection Observer Animation */

.fade-in {
    opacity: 1;
    animation: fadeInUp 1s forwards;
}

/* Transition Effects */

.fade-in {
    opacity: 1;
    animation: fadeInUp 1s forwards;
}

/* Transition Effects */

@keyframes fadeInUp {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeOutDown {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

.fade-in {
    animation: fadeInUp 1s forwards;
}

.fade-out {
    animation: fadeOutDown 1s forwards;
}

.pillar-content {
    font-size: 1em;
    animation: fadeInUp 1s forwards;
    padding-top: 1em;
    padding-bottom: 2em;
}

@media (max-width: 768px) {
    /* Mobile styles here */

    h1 {
        font-size: 5rem;
    }
    .iconned_button {
        margin: 20px 0px 20px 0;
        position: relative;
        width: 120%; /* Just an example */
        left: -10%;
    }
    .iconned_button_img {
        height: 18px;
        flex-shrink: 0; /* prevents the image from shrinking*/
        padding: 6px;
    }

    .ignore_if_mobile {
        display: none;
    }

    .flex_box {
        flex-direction: column;
        overflow: none;
        max-height: initial;
        height: 900px;
    }
}
